  align-content: space-evenly;
  flex-direction: column;
}
.loader_div{
  width: 100%;
  height: 150vh;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 10;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader_div_none{
  display: none;
}
.loader_text{
  font-size: 20px;
  color: #fff;
  font-weight: 700;
  text-transform: capitalize;
  font-family: "Nunito Sans", sans-serif;
}
.loader {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  border: 5px solid;
  position: absolute;
  top: 45vh;
  border-color: #FFF #FFF transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after {
  content: '';  
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 4px solid;
  border-color: transparent #5c9ce5 #5c9ce5;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  animation: rotationBack 0.5s linear infinite;
  transform-origin: center center;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
} 
    
@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
    

